export default {
	data() {
		return {
			title: 'Hello',
			list: [],
			pagination: 1,
			loadingText: '上拉加载更多',
			ph: 0,
			scrollHeight: 0,
			showanimation: false
		}
	},
	onLoad() {
		let that = this
		uni.getSystemInfo({
			success: function(res) { // res - 各种参数
				that.scrollHeight = res.windowHeight
				console.log("onload"+that.scrollHeight)
			}
		});
		this.clickdetail()
	},
	// onReady() {
	// 	let that=this;
	// 	uni.getSystemInfo({ //调用uni-app接口获取屏幕高度
	// 		success(res) { //成功回调函数
	// 			that.ph=res.windowHeight //windoHeight为窗口高度，主要使用的是这个
	// 			console.log("onready"+that.ph)
	// 			let titleH=uni.createSelectorQuery().select(".scroll-view"); //想要获取高度的元素名（class/id）
	// 			titleH.boundingClientRect(data=>{
	// 				that.scrollHeight=data.height  //计算高度：元素高度=窗口高度-元素距离顶部的距离（data.top）
	// 				console.log("onready"+that.scrollHeight)
	// 			}).exec()
	// 		}
	// 	})
	// 	this.clickdetail()
	// },
	methods: {
		scroll() {
			uni.startPullDownRefresh();
		},
		onPullDownRefresh() {
			this.pagination = 1;
			this.clickdetail();
			setTimeout(function() {
				uni.stopPullDownRefresh();
			}, 1000);
		},
		clickdetail: function(e) {
			uni.request({
				url: "https://m.jianzhimao.com/ajax/get-job-list?page=" + this.pagination +
					"&prov=&city=%E5%B9%BF%E5%B7%9E&area=&type=&time=",
				// url: "https://m.jianzhimao.com/job/pageDirect?page=" + this.pagination + "&city=%E5%B9%BF%E5%B7%9E", //仅为示例，并非真实接口地址。
				method: "GET",
				dataType: 'json',
				success: (res) => {
					this.list = res.data.data.list
				}
			});
		},
		lower: function(e) {
			this.pagination = this.pagination + 1
			this.showanimation=true
			this.loadingText = '加载中'
			uni.request({
				url: "https://m.jianzhimao.com/ajax/get-job-list?page=" + this.pagination +
					"&prov=&city=%E5%B9%BF%E5%B7%9E&area=&type=&time=",
				method: "GET",

				dataType: 'json',
				success: (res) => {

					if (res.data.data.length <= 0) {
						this.loadingText = '我是有底线的'
						this.showanimation=false
						return false
					}
					this.list = this.list.concat(res.data.data.list),
						this.loadingText = '上拉加载更多'
						this.showanimation=false

				}
			});

		},
		openinfo(e) {
			console.log(e)
			if (e == 1) {
				uni.navigateTo({
					url: '../common/common',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if (e == 2) {
				uni.navigateTo({
					url: '../zhizhao/zhizhao',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
			if (e == 3) {
				uni.navigateTo({
					url: '../miaorenwu/miaorenwu',
					success: res => {},
					fail: () => {},
					complete: () => {}
				})
			}

		}
	}
}
